import Vue from 'vue'
import Vuex from 'vuex'
import clone from "@/lib/clone"
import createId from "@/lib/createId"
import _ from 'lodash'
import { nanoid } from 'nanoid'

Vue.use(Vuex) //storevue.prototype,storevuemain.ts

const store = new Vuex.Store({
  state: {
    recordList: [],
    tagList: [],
    currentRecord: undefined,
    editRecord: undefined,
    currentTag: undefined,
    createRecordError: null,
    createTagError: null,
    updateTagError: null,
  } as RootState,
  mutations: { //method ,,payload
    fetchRecords(state) {
      state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]')
    },
    fetcheditRecord(state){
      state.editRecord = JSON.parse(window.localStorage.getItem('editItem') || '[]')
    },
    saveRecords(state) {
      window.localStorage.setItem('recordList', JSON.stringify(state.recordList))
    },
    savaEditRecord(state){
      window.localStorage.setItem('editItem',JSON.stringify(state.editRecord))
    },
    createRecord(state, record: RecordItem) {
      const id = nanoid()
      Object.defineProperty(record, 'id', {
        value: id,
        writable: false,
        enumerable: true //true
      })
      state.recordList.push(record);
      store.commit('saveRecords')
    },
    setCurrentRecord(state, id: string) {
      state.currentRecord = state.recordList.filter(r => r.id === id)[0]
    },
    setEditRecord(state,record){
      state.editRecord = record
    },
    deleteRecord(state, id: string) {
      let index = -1
      for (let i = 0; i < state.recordList.length; i++) {
        if (state.recordList[i].id === id) {
          index = i
          break
        }
      }
      state.recordList.splice(index, 1)
      store.commit('saveRecords')
    },
    updateRecord(state, record) {
      const { id,amount, } = record
      let index = -1
      for (let i = 0; i < state.recordList.length; i++) {
        if (state.recordList[i].id === id) {
          index = i
          break
        }
      }
      state.recordList[index] = record 
      store.commit('saveRecords')
      window.localStorage.removeItem('editItem')
    },
    setCurrentTag(state, id: string) {
      state.currentTag = state.tagList.filter(t => t.id === id)[0]
    },
    fetchTags(state) {
      state.tagList = JSON.parse(window.localStorage.getItem('tagsList') || '[]')
      if (!state.tagList || state.tagList.length === 0) {
        store.commit('createTag', { name: '', iconName: 'canying', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'yule', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'tongxun', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'jiaotong', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'yanjiu', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'shuma', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'fushi', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'lvxing', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'bangong', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'shuiguo', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'yundong', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'ertong', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'laoren', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'weixiu', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'juanzeng', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'caipiao', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'meirong', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'shucai', mold: '-' })
        store.commit('createTag', { name: '', iconName: 'gongzi', mold: '+' })
        store.commit('createTag', { name: '', iconName: 'jianzhi', mold: '+' })
        store.commit('createTag', { name: '', iconName: 'licai', mold: '+' })
        store.commit('createTag', { name: '', iconName: 'lijin', mold: '+' })
        store.commit('createTag', { name: '', iconName: 'qita', mold: '+' })
      }
    },
    createTag(state, payload: { name: string, iconName: string, mold: string }) {
      const { name, iconName, mold } = payload
      state.createTagError = null
      const names = state.tagList.map(item => item.name)
      const id = createId().toString()
      if (names.indexOf(name) >= 0) {
        state.createTagError = new Error('tag name duplicated')
        return
      }
      state.tagList.push({ id: id, name: name, iconName: iconName, mold: mold })
      store.commit('saveTag')
    },
    saveTag(state) {
      window.localStorage.setItem('tagsList', JSON.stringify(state.tagList))
    },
    updateTag(state, payload: { id: string, name: string, iconName: string, mold: string }) {
      const { id, name, iconName, mold } = payload
      state.updateTagError = null
      const idList = state.tagList.map(item => item.id)
      if (idList.indexOf(id) >= 0) {
        const index = state.tagList.findIndex(item => item.id === id)
        const tagList2 = clone(state.tagList)
        tagList2.splice(index, 1) //splice
        const names = tagList2.map(item => item.name)
        if (names.indexOf(name) >= 0) {
          state.updateTagError = new Error('tag name duplicated')
          return
        } else {
          const tag = state.tagList.filter(item => item.id === id)[0]
          tag.name = name
          tag.iconName = iconName
          tag.mold = mold
          store.commit('saveTag')
        }
      } else {
        return 'not found'
      }
    },
    removeTag(state, id: string) {
      let index = -1
      for (let i = 0; i < state.tagList.length; i++) {
        if (state.tagList[i].id === id) {
          index = i
          break
        }
      }
      state.tagList.splice(index, 1)
      store.commit('saveTag')
    },
  },
})

export default store;